.content_2 {
  padding: 0 0 10px;
  position: relative;
}
.content_2 .ant-input-search {
  width: 217px;
  margin-right: 30px;
  height: 28px;
}
.content_2 .ant-input-search input {
  height: 32px;
  background: none;
  border-radius: 13.5px;
}
.content_2 .ant-input-search input::-webkit-input-placeholder {
  color: #888;
}
.content_2 .ant-input-search input:-moz-placeholder {
  color: #888;
}
.content_2 .ant-input-search input::-moz-placeholder {
  color: #888;
}
.content_2 .ant-input-search input:-ms-input-placeholder {
  color: #888;
}
.content_2 .ant-modal .ant-modal-confirm-body-wrapper .ant-modal-confirm-btns .ant-btn-primary {
  background: #3E78ED !important;
}
.content_2 .top {
  display: flex;
  justify-content: flex-end;
  padding: 15px 20px;
}
.content_2 .top .ant-btn {
  background: #3E78ED;
  color: #fff;
  border: none;
  border-radius: 5px;
}
.content_2 .top .ant-select-selection {
  width: 180px;
  height: 30px;
  background: none;
  line-height: 30px;
  border-radius: 2px;
  margin-right: 20px;
}
.content_2 .content {
  background: #fff;
}
.content_2 .content .left {
  margin-right: 40px;
  width: 300px;
  padding: 10px;
  background: #fff;
  min-height: calc(100vh - 341px);
}
.content_2 .content .left ul {
  width: 275px;
  min-height: 0 !important;
  margin: 0 0 10px 0;
}
.content_2 .content .left ul .anticon {
  color: #3E78ED;
  display: none;
}
.content_2 .content .left .ant-menu-item {
  display: flex;
  align-items: center;
}
.content_2 .content .left .ant-menu-item span:first-child {
  flex: 1;
}
.content_2 .content .left .ant-input {
  width: 100%;
  height: 30px;
  background: #F3F3F3;
  border-radius: 13.5px;
  border: none;
}
.content_2 .content .left .search-student {
  padding: 2px 10px 10px;
  border-bottom: 1px solid #f3f3f3;
  margin-bottom: 10px;
}
.content_2 .content .left .ant-menu-item-selected {
  background: none !important;
  font-weight: bold;
  color: #2A283D;
}
.content_2 .content .left .ant-menu-item-selected::before {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  background: #3E78ED;
  border-radius: 50%;
  position: absolute;
  left: 20px;
}
.content_2 .content .left .ant-menu-item-selected:hover {
  color: #2A283D;
}
.content_2 .content .left .ant-menu-item:hover {
  background: #F7F9FB;
  color: #888;
}
.content_2 .content .left .ant-menu-item:hover .anticon {
  display: inline-block;
}
.content_2 .content .right {
  width: 100%;
  box-shadow: 0px 0 3px #eee;
  background: #fff;
  color: #2A283D;
  padding: 5px 10px 60px;
  min-height: calc(100vh - 400px);
}
.content_2 .content .right th,
.content_2 .content .right td {
  text-align: center !important;
  padding: 0 16px;
  border: none;
}
.content_2 .content .right .ant-btn {
  background: none;
  border: none;
  color: #3E78ED;
  box-shadow: none;
}
.content_2 .content .right .ant-table-row {
  height: 46px !important;
}
.content_2 .content .right .ant-table-row:nth-child(odd) {
  background: #F7F9FB;
}
.content_2 .content .right tr:hover td {
  background: none !important;
}
.content_2 .content .right th {
  background: #fff !important;
}
.content_2 .anticon {
  color: #666;
  margin-left: 5px;
}
.content_2 .group-name {
  display: inline-block;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
}
.flex_style_lh {
  display: flex;
  justify-content: space-between;
}
.flex_style_lh .ant-select-selection {
  width: 180px;
  height: 30px;
  border-radius: 2px;
}
.flex_style_lh .ant-input-search {
  width: 220px;
}
.flex_style_lh .ant-input-search .ant-input {
  height: 30px;
  border-radius: 2px;
}
.add_student_style_l {
  position: relative;
  padding-bottom: 50px;
}
.add_student_style_l .pagination-style-l {
  bottom: -10px;
}
.add_student_style_l .ant-checkbox-indeterminate .ant-checkbox-inner::after {
  background: #3E78ED;
}
.add_student_style_l .ant-checkbox-checked .ant-checkbox-inner {
  background: #3E78ED;
  border-color: #3E78ED;
}
